<template>
  <div class="teacher">


    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/school_list' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{path:'/teacher_list?id='+$route.query.id}">学校管理({{detail.name}})</el-breadcrumb-item>
      <el-breadcrumb-item>老师列表</el-breadcrumb-item>
    </el-breadcrumb>
    <br/>




      <el-form :inline="true" class="demo-form-inline" :loading="loading">

        <el-form-item>
          <el-form-item label="选择时间">
            <el-date-picker
              v-model="date"
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form-item>
        <el-form-item>
          <download :data="list" title="下载数据"></download>
        </el-form-item>

      </el-form>



      <el-table
        :data="list"
        style="width: 100%"
        border
        size="mini"
        v-loading="loading"
      >
        <el-table-column v-for=" h in header " :prop="h" :label="h "></el-table-column>


      </el-table>













    <br>
    <el-pagination

      layout="prev, pager, next"
      :total="total"

      :current-page="current_page"
      :page-size="page_size"
    >
    </el-pagination>
  </div>
</template>

<script>

  import ElButtonGroup from "../../node_modules/element-ui/packages/button/src/button-group.vue";
  import request from './config'
  import ElButton from "../../node_modules/element-ui/packages/button/src/button.vue";
  import ElCol from "element-ui/packages/col/src/col";

  import req from 'axios'
  import moment from 'moment'
  import download from './excel-download.vue'
export default {
  components: {
    download,
    ElCol,
    ElButton,
    ElButtonGroup},
  name: 'data-export',
  data () {
    return {
      detail:{},
      list:[],
      header:[],
      total:0,
      search:'',
      page_size:10,
      current_page:1,
      loading:false,
      date:[
        moment().subtract(1,'month').format('YYYY-MM-DD'),
        moment().format('YYYY-MM-DD'),
      ],

    }
  },

  watch:{
      current_page(){
          this.fetch()
      },
      date(){
        this.fetch()
      }
  },

  methods:{


    handlePreview(){},
    handleRemove(){},
      change_page(p){
          console.log(p)
      },



      async fetch(){
          this.header =[]
          var _limit = this.page_size,
              _page = this.current_page,
              search = this.search,
              that = this

          this.loading = true
          console.log(window.school,'window.school')
          var response = await request.get('/api/excel/',{
            params:{
              school_id:that.$route.query.id,
              start:moment(that.date[0]).format('YYYY-MM-DD'),
              end:moment(that.date[1]).format('YYYY-MM-DD')
            }
          })

          var exam = response.data.exam
          var list = []
          for(var i=0;i<response.data.list.length;i++){
            var item = response.data.list[i]
            item.exam_list = item.exam_list || [{id:item.exam_id,total:item.total,desc:item.desc}]
            var same_teacher = list.find(function (obj) {
              return obj.teacher_id == item.teacher_id
            })
            if(same_teacher){
              same_teacher.exam_list.push({id:item.exam_id,total:item.total,desc:item.desc})
            }else {
              list.push(item)
            }
          }

        that.header.push('身份证')
        that.header.push('银行卡号')
        that.header.push('姓名')
        that.header.push('电话')
        that.header.push('所属学校')

        response.data.exam.map(function (obj) {
          that.header.push(obj.desc)

        })
        that.header.push('总计')
          list = list.map(function (item) {
            response.data.exam.map(function (obj) {
              item[obj.desc] = 0

            })
            item.exam_list.map(function (obj) {
              item[obj.desc] = obj.total
            })

            var sum = 0
            for(var i=0;i<item.exam_list.length;i++){
              sum+=item.exam_list[i].total
            }

            item['总计']=sum

            item['身份证']=item.teacher.idcard
            item['银行卡号']=item.teacher.bankcard
            item['姓名']=item.teacher.name
            item['电话']=item.teacher.phone
            item['所属学校']=item.teacher.school_name

            delete item.teacher
            delete item.desc
            delete item.exam_list
            delete item.create_time
            delete item.exam_id
            delete item.school_total
            delete item.total
            delete item.teacher_id
            delete item.update_time
            return item
          })

          console.log(list,'list')



          this.list = list


        this.loading = false
      },

    async fetch_school(){
      var id = this.$route.query.id
      var response = await request.get('/api/school/'+id+'/')
      this.detail = response.data
      console.log(response.data)
    },

  },
  mounted(){
      this.fetch()
      this.fetch_school()
  }


}

</script>

